export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			title: 'picker',
			array: ['本人', '父亲', '母亲', '祖父母', '外祖父母', '叔叔姑姑', '兄弟姐妹', '舅舅阿姨', '其他', '亲属', '父母'],
			index: 0,
			hospitals: ['重庆医科大学附属儿童医院儿保科', '重庆妇幼保健院儿保科', '涪陵区妇幼保健院儿保科', '万州区妇幼保健院儿保科', '长寿区妇幼保健院儿保科',
				'沙坪坝区小龙坎社区卫生服务中心', '大溪沟街道社区卫生服务中心', '渝中区两路口街道社区卫生服务中心', '宜宾长宁县长宁填社区卫生中心', '其它'
			],
			indexHospitals: 0,
			date: currentDate,
			time: '12:01'
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		confirmTheChange(){
			uni.showToast({
			    title: '成功',
			    duration: 2000,
			});
			setTimeout(()=>{
				uni.navigateBack({
					url:'myInformation',
				})
			},500)
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
		},
		bindPickerChangeHospitals: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.indexHospitals = e.target.value
		},
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		bindTimeChange: function(e) {
			this.time = e.target.value
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	}
}
